const axios = require("axios");
const jwt = require('jsonwebtoken');

export const getSales = async (host, page = 1, {startDate, endDate}, token) => {
    const user = jwt.decode(token);
    const data = await axios.get(
        `${host}:5000/sellers/${user.id}/list?page=${page}&startDate=${startDate}&endDate=${endDate}`,
        {
            headers: {
                Authorization: "Bearer " + token,
            }
        }
    )
    .then(response => {
        if(response.status === 200)
            return response.data;
    })
    .catch(console.log);

    return data;
}

