export const login = async (host, {username, password}) => 
    axios.post(host + ':5000/login', {username, password}, {
        validateStatus: function (status) {
            return status < 500;
          }
    });


export const addSale = async (host, load = {}, token ) => {
    const user = jwt.decode(token);

    const body = {
        code: load.code,
        value: Number(load.value),
    }
    const data = await axios.post(host, host + ':5000/sellers/' + user.id + '/add', body,
    {
        headers: {
            Authorization: "Bearer " + token,
        }
    }
    )
    .then(response => {
        if(response.status === 200)
            return response.data;
    })
    .catch(console.log);

    return data;
}

export const addSeller = async (host, load = {}, token ) => {
    const body = {
        code: load.code,
        role: load.role,
        name: load.name,
        password: load.password,
    }
    const data = await axios.post(host, host + ':5000/sellers', body,
    {
        headers: {
            Authorization: "Bearer " + token,
        }
    }
    )
    .then(response => {
        if(response.status === 200)
            return response.data;
    })
    .catch(console.log);

    return data;
}

