export const deleteSales = async (host, sales = [], token) => {
    await Promise.all(sales.map(async sale => {
        return await axios.delete(host + ':5000/sales/' + sale.id,
        {
            headers: {
                Authorization: "Bearer " + token,
            }
        }
        )
        .then(response => {
            if(response.status === 204)
                return;
        })
        .catch(console.log);
    }));

    return;
}

export const alterSales = async (host, sales = [], token) => {
    await Promise.all(sales.map(async sale => {
        return await axios.patch(host + ':5000/sales/' + sale.id, sale,
        {
            headers: {
                Authorization: "Bearer " + token,
            }
        })
        .then(response => {
            if(response.status === 204)
                return;
        })
        .catch(console.log);
    }));

    return;
}

export const paySales = async (host, sales = [], token) => {
    await Promise.all(sales.map(async sale => {
        return await axios.patch(
        host + ':5000/sales/' + sale.id,
        {
            paid: true
        },{
            headers: {
                Authorization: "Bearer " + token,
            }
        })
        .then(response => {
            if(response.status === 200)
                return;
        })
        .catch(console.log);
    }));

    return;
}

